import { Button, Swiper } from "@taroify/core"
import * as React from "react"
import { useState } from "react"

function SwiperWithValues() {
  const [items] = useState([0, 1, 2, 3])
  const [value, setValue] = useState(0)

  return (
    <>
      {items.map((item) => (
        <Button key={item} onClick={() => setValue(item)}>
          {item}
        </Button>
      ))}
      <Swiper autoplay={4000} value={value} onChange={setValue}>
        <Swiper.Indicator />
        {items.map((item) => (
          <Swiper.Item key={item} style={{ height: "100px", background: "#39a9ed" }}>
            {item}
          </Swiper.Item>
        ))}
      </Swiper>
    </>
  )
}

function Index() {
  return <SwiperWithValues />
}

export default Index
